body {
  font-family: "Sansation", arial;
  box-sizing: border-box;
  background-color: #27343a;
  color: #3a828e;
  width: 100vw;
  height: 100vh;
  font-size: 20px;
  margin: 0;
  padding: 0;
  filter: blur(0.25px);
}
body::before {
  pointer-events: none;
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("https://cdn.realsgii2.dev/pathOS/ui/scanline/scanline.png");
  opacity: 0.2;
}
body::after {
  pointer-events: none;
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("https://cdn.realsgii2.dev/pathOS/ui/background/station.png");
  background-size: 80%;
  background-repeat: no-repeat;
  opacity: 0.15;
  z-index: 999;
}

*,
*::before,
*::after {
  font-family: inherit;
  box-sizing: inherit;
  cursor: url("https://cdn.realsgii2.dev/pathOS/ui/v2/cursor/cursor.png"), auto !important;
}
